"use client";

import { useParams, useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import Link from "next/link";
import Image from "next/image";

import {
  fetchAllProducts,
  fetchImagesByProductId,
  fetchVariantsByProductId,
} from "@/lib/db";
import { getPrimaryImageUrl } from "@/lib/utils/getPrimaryImageUrl"; // ⬅️ if your path is different, fix it
import { getBestVariant } from "@/lib/utils/getBestVariant";       // ⬅️ if your path is different, fix it
import AddToCartButton from "@/components/AddToCartButton";        // ⬅️ if your path is different, fix it

export default function ResultsPage() {
  const router = useRouter();
  const { tag } = useParams();
  const decodedTag = decodeURIComponent(tag as string);

  const [loading, setLoading] = useState(true);
  const [results, setResults] = useState<any[]>([]);

  useEffect(() => {
    async function run() {
      setLoading(true);
      const all = await fetchAllProducts();

      const filtered = all.filter((p: any) =>
        Array.isArray(p.tags) ? p.tags.includes(decodedTag) : false
      );

      setResults(filtered);
      setLoading(false);
    }
    run();
  }, [decodedTag]);

  return (
    <main className="bg-white min-h-screen text-black mx-auto max-w-md">
      {/* HEADER */}
      <header className="sticky top-0 z-50 bg-white px-4 py-3 flex items-center gap-3 border-b">
        <button
          onClick={() => router.back()}
          className="h-9 w-9 rounded-full bg-[#0A5F91] text-white grid place-items-center"
        >
          ←
        </button>
        <div className="font-semibold text-lg truncate">{decodedTag}</div>
      </header>

      {/* BODY */}
      {loading ? (
        <div className="text-center py-10 text-gray-500">Loading...</div>
      ) : results.length === 0 ? (
        <div className="p-6 text-center text-gray-700">
          <p>No results for <b>{decodedTag}</b>.</p>
          <a
            href={`https://wa.me/252622073874?text=${encodeURIComponent(
              `Hello, I can't find: "${decodedTag}". Can you help me?`
            )}`}
            target="_blank"
            rel="noopener noreferrer"
            className="block mt-4 bg-[#0B6EA9] text-white font-semibold px-4 py-2 rounded-full"
          >
            WhatsApp Us
          </a>
        </div>
      ) : (
        <section className="grid grid-cols-2 gap-3 p-3">
          {results.map((product: any) => {
            const price = getBestVariant(product.id, product.base_price)?.price;
            const mrp = getBestVariant(product.id, product.base_price)?.mrp;
            const img = getPrimaryImageUrl(product.id);
            const offPct = mrp ? Math.round(((mrp - price) / mrp) * 100) : null;

            return (
              <div key={product.id} className="border rounded-2xl p-2 pb-4 bg-white">
                <Link href={`/product/${product.slug}`}>
                  <div className="relative mb-2">
                    {offPct && (
                      <span className="absolute top-2 left-2 bg-red-600 text-white text-xs px-2 py-1 rounded-full font-bold">
                        {offPct}% off
                      </span>
                    )}

                    <div className="w-full h-36 bg-gray-100 rounded-xl grid place-items-center overflow-hidden">
                      <Image
                        src={img}
                        alt={product.name}
                        width={200}
                        height={200}
                        className="object-contain"
                      />
                    </div>
                  </div>

                  <div className="text-sm font-semibold leading-tight line-clamp-2 mb-1 px-1">
                    {product.name}
                  </div>
                </Link>

                <div className="px-1">
                  <div className="flex items-end gap-2">
                    <span className="text-lg font-bold text-gray-900">{price} AED</span>
                    {mrp && <span className="text-sm line-through text-gray-400">{mrp} AED</span>}
                  </div>

                  <div className="mt-2 flex justify-end">
                    <AddToCartButton productId={product.id} />
                  </div>
                </div>
              </div>
            );
          })}
        </section>
      )}
    </main>
  );
}